.input-form {
  background-color: bisque;
}

.submit-button {
  color: lightyellow;
  border-radius: 5px;
  background: darkblue;
  -webkit-transition: 0.3s color ease-in-out, 0.3 background ease-in-out;
  transition: 0.3s color ease-in-out, 0.3 background ease-in-out;
  margin-bottom: 4vh;
  -webkit-transition: 0.2s opacity linear;
  transition: 0.2s opacity linear;
}

.submit-button:disabled {
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
  opacity: 0.5;
  -webkit-transition: 0.2s opacity linear;
  transition: 0.2s opacity linear;
}

.Weather {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  /* background-color: rgb(102, 102, 204); */
  background: -webkit-repeating-linear-gradient(
    left,
    rgba(102, 102, 204, 0.5) 10%,
    rgba(102, 102, 204, 1) 20%,
    rgba(102, 102, 204, 0.5) 30%
  );
  background: repeating-linear-gradient(
    to right,
    rgba(102, 102, 204, 0.5) 10%,
    rgba(102, 102, 204, 1) 20%,
    rgba(102, 102, 204, 0.5) 30%
  );
  font-weight: bold;
  color: beige;
  height: 20vh;
  padding-left: 4vh;
  padding-right: 4vh;
  /* border: lightblue solid; */
}

