.App-header {
  background-color: lightblue;
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(8px + 2vmin);
  color: black;
}

.logo {
  display: flex;
  flex-direction: column;
  height: 10vh;
  margin-bottom: 4vh;
  animation: img-spin infinite 20s linear;
  pointer-events: none;
}

@keyframes img-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
